import { Record, Union } from "./.fable/fable-library.3.1.12/Types.js";
import { record_type, bool_type, list_type, union_type, string_type, array_type, class_type } from "./.fable/fable-library.3.1.12/Reflection.js";
import { printf, toText } from "./.fable/fable-library.3.1.12/String.js";

export class AlbumState extends Union {
    constructor(tag, ...fields) {
        super();
        this.tag = (tag | 0);
        this.fields = fields;
    }
    cases() {
        return ["Loading", "Loaded", "LoadFailed"];
    }
}

export function AlbumState$reflection() {
    return union_type("Model.AlbumState", [], AlbumState, () => [[], [["Item", array_type(class_type("Album.IMediaInfo"))]], [["Item", string_type]]]);
}

export function AlbumState__get_AsString(this$) {
    switch (this$.tag) {
        case 1: {
            return "Album Loaded";
        }
        case 2: {
            return toText(printf("Album Load Failed (%s)"))(this$.fields[0]);
        }
        default: {
            return "Album Loading";
        }
    }
}

export class ModelState extends Union {
    constructor(tag, ...fields) {
        super();
        this.tag = (tag | 0);
        this.fields = fields;
    }
    cases() {
        return ["Loading", "Loaded", "Error"];
    }
}

export function ModelState$reflection() {
    return union_type("Model.ModelState", [], ModelState, () => [[], [], [["Item", list_type(class_type("System.Exception"))]]]);
}

export class Model extends Record {
    constructor(state, albumState, backgroundVideoIsLoaded, lang) {
        super();
        this.state = state;
        this.albumState = albumState;
        this.backgroundVideoIsLoaded = backgroundVideoIsLoaded;
        this.lang = lang;
    }
}

export function Model$reflection() {
    return record_type("Model.Model", [], Model, () => [["state", ModelState$reflection()], ["albumState", AlbumState$reflection()], ["backgroundVideoIsLoaded", bool_type], ["lang", string_type]]);
}

export class Msg extends Union {
    constructor(tag, ...fields) {
        super();
        this.tag = (tag | 0);
        this.fields = fields;
    }
    cases() {
        return ["Ignore", "InitTaskCompleted", "InitError", "BackgroundVideoLoaded", "LoadAlbumResponse", "SwitchLanguage"];
    }
}

export function Msg$reflection() {
    return union_type("Model.Msg", [], Msg, () => [[], [], [["Item", class_type("System.Exception")]], [], [["Item", class_type("Album.IResult")]], [["Item", string_type]]]);
}

export function initModel(arg) {
    return new Model(new ModelState(0), new AlbumState(0), false, "unspecified");
}

